import { Axios } from 'axios'
import {
  WalletPerformance,
  WalletPerformanceClientModel,
  WalletPerformanceRoutes,
  WalletPerformancesSearchQuery,
} from '../wallet-performance.model'
import { ApiEntity } from '../../core'

export class WalletPerformanceClient
  extends ApiEntity
  implements WalletPerformanceClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Search wallet performances by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for search wallet performances
   */
  public search(searchParams?: WalletPerformancesSearchQuery) {
    return this._search<WalletPerformance>(
      WalletPerformanceRoutes.v1Search,
      searchParams
    )
  }

  /**
   * Search all epoch by params
   * @param searchParams - the vault
   * @returns
   */
  public searchAll(searchParams?: WalletPerformancesSearchQuery) {
    return this._searchAll<WalletPerformance>(
      WalletPerformanceRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List wallet performances by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for list wallet performances
   */
  public list(searchParams?: WalletPerformancesSearchQuery) {
    return this._list<WalletPerformance>(
      WalletPerformanceRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List all wallet performances by params
   * @param searchParams - the search campaigns search params
   * @returns the promise for list wallet performances
   */
  public listAll(searchParams?: WalletPerformancesSearchQuery) {
    return this._listAll<WalletPerformance>(
      WalletPerformanceRoutes.v1Search,
      searchParams
    )
  }
}
